import "./Home.css"

import React from "react"
import montiLogo from "./bundledFiles/montiLogo.png"
import { motion } from 'framer-motion'

const audioPlayer = new Audio()
// audioPlayer.autoplay = true

const Home = ({ start, settings }) => {
    return <div className="home">
        <button
            className="settings"
            onClick={() => {
                audioPlayer.src = "data:audio/wav;base64,UklGRigAAABXQVZFZm10IBIAAAABAAEARKwAAIhYAQACABAAAABkYXRhAgAAAAEA"
                settings()
            }}
        >
            <img src="./icons/settings.svg" height='50rem' />
        </button>
        <div className="title-logo">
            <img src={montiLogo} id='montiLogo' alt="logo" />
        </div>
        <motion.button
            whileTap={{ scale: 0.9, filter: 'brightness(50%)' }}
            transition={{ duration: .1 }}
            className="startButton"
            style={{ width: '15rem' }}
            onClick={() => start()}
        >
            <img src="./icons/play.svg" height='70rem' />
        </motion.button>
    </div>
}

export default Home
export { audioPlayer }