import { React, useState, useEffect, useRef } from 'react';
import { motion } from 'framer-motion';

import './level.css';
import { levels, colorByLetter } from './utils/levelData';
import letterSounds from './utils/sounds';
import levelSounds from './utils/levelSounds';
import { audioPlayer } from './Home';

const DRAW_ALL_DOTS = false;

const TRACE_FUDGE_FACTOR = 1.5;
const HIT_FUDGE_FACTOR = 2;

const DRAW_LINE_WIDTH = 35;

const [canvasWidth, canvasHeight] = [window.innerWidth, window.innerHeight];

var isAnimating, imgScale, context;

const transformStroke = (stroke, letterSize, pointsScale) => {
    const xMove = (canvasWidth - letterSize[0]) / 2;
    const yMove = (canvasHeight - letterSize[1]) / 2;

    const out = [];
    for (let point of stroke) {
        out.push([pointsScale[0] * point[0] + xMove, -1 * pointsScale[1] * point[1] + yMove]);
    }
    return out;
};

const drawTarget = (tracePointIndex, traceStrokeIndex, transformStrokes, scaleFactor) => {
    context.current.fillStyle = 'green';
    context.current.beginPath();
    if (transformStrokes && transformStrokes[traceStrokeIndex]) {
        const tracePoint = transformStrokes[traceStrokeIndex][tracePointIndex];
        context.current.ellipse(tracePoint[0], tracePoint[1], 17 * scaleFactor, 17 * scaleFactor, 0, 0, Math.PI * 2);
        context.current.fill();
    }
};

const checkDrawnPoint = (prevD2ToPoint, nextPoint, pointToCheck, scaleFactor) => {
    const dist = (pointToCheck.x - nextPoint[0]) ** 2 + (pointToCheck.y - nextPoint[1]) ** 2;
    if (dist < (HIT_FUDGE_FACTOR * scaleFactor) ** 2) {
        return {
            good: true,
            hit: true,
            dist: dist
        };
    }

    if (Math.max(dist - (TRACE_FUDGE_FACTOR * scaleFactor) ** 2, 0) < prevD2ToPoint) {
        return {
            good: true,
            hit: false,
            dist: dist
        };
    }

    return {
        good: false,
        hit: false,
        dist: dist
    };
};

const advanceLetter = (letterInd, setLetterInd, traceStrokeInd, tracePointInd, resetPrevDist) => {
    resetPrevDist();
    context.current.clearRect(0, 0, canvas.width, canvas.height);
    traceStrokeInd.current = 0;
    tracePointInd.current = 0;
    setLetterInd(letterInd + 1);
};

const advanceLetterWithDelay = (letterInd, setLetterInd, traceStrokeInd, tracePointInd, resetPrevDist, delay) => {
    setTimeout(() => {
        advanceLetter(letterInd, setLetterInd, traceStrokeInd, tracePointInd, resetPrevDist);
    }, delay * 1000); // delay is in seconds, so multiply by 1000 to convert to milliseconds
};

const advancePoint = (tracePointIndRef, traceStrokeIndRef, transformedStrokes, resetPrevDist, betweenStrokes) => {
    resetPrevDist();
    betweenStrokes.current = false;
    tracePointIndRef.current += 1;
    if (tracePointIndRef.current >= transformedStrokes[traceStrokeIndRef.current].length) {
        tracePointIndRef.current = 0;
        traceStrokeIndRef.current += 1;
        betweenStrokes.current = true;
    }
};

const saveLetterAttrib = (performanceDataCurrent, letter, key, value) => {
    performanceDataCurrent.letters[letter] = performanceDataCurrent.letters[letter] || {};
    performanceDataCurrent.letters[letter][key] = value;
};

const getLetterAttrib = (performanceDataCurrent, letter, key) => {
    performanceDataCurrent.letters[letter] = performanceDataCurrent.letters[letter] || {};
    return performanceDataCurrent.letters[letter][key];
};

const calcDist = (p1, p2) => {
    return [Math.sqrt((p2[0] - p1[0]) ** 2 + (p2[1] - p1[1]) ** 2), p2[0] - p1[0], p2[1] - p1[1]];
};

const postAnimate = async (transformedStrokes) => {
    await new Promise((resolve) => setTimeout(resolve, 0.3 * 1000));
    isAnimating.current = false;
    context.current.clearRect(0, 0, canvas.width, canvas.height);
    drawTarget(0, 0, transformedStrokes, imgScale.current);
    return;
};

const animationFunc = async (transformedStrokes, animationCoords, strokeInd, pointInd, startPoint) => {
    context.current.beginPath();
    context.current.moveTo(startPoint[0], startPoint[1]);
    if (transformedStrokes[strokeInd].length === 1) {
        context.current.ellipse(transformedStrokes[strokeInd][0][0], transformedStrokes[strokeInd][0][1], 17 * imgScale.current / 3, 17 * imgScale.current / 3, 0, 0, Math.PI * 2);
    } else {
        context.current.lineTo(animationCoords[strokeInd][pointInd][0], animationCoords[strokeInd][pointInd][1]);
    }
    context.current.strokeStyle = 'green';
    context.current.lineWidth = 34 * imgScale.current;
    context.current.lineCap = 'round';
    context.current.stroke();

    startPoint = animationCoords[strokeInd][pointInd];
    pointInd++;
    if (pointInd >= animationCoords[strokeInd].length) {
        pointInd = 0;
        strokeInd++;
        startPoint = transformedStrokes[strokeInd] ? transformedStrokes[strokeInd][0] : null;
        await new Promise((resolve) => setTimeout(resolve, 0.3 * 1000));
    }
    if (strokeInd >= animationCoords.length) {
        return postAnimate(transformedStrokes);
    }

    requestAnimationFrame(async () => await animationFunc(transformedStrokes, animationCoords, strokeInd, pointInd, startPoint));
};

const UNITS_PER_SECOND = 40;
const animateStrokes = (transformedStrokes, pointsScale, fps) => {
    const framesPerPixel = (1 / UNITS_PER_SECOND) * (1 / pointsScale) * fps;

    let pixelDist, xDist, yDist, numFrames, xStep, yStep;
    let animationCoords = [];

    for (let stroke of transformedStrokes) {
        let strokeCoords = [];
        for (let [i, point] of stroke.entries()) {
            if (i + 1 < stroke.length) {
                [pixelDist, xDist, yDist] = calcDist(point, stroke[i + 1]);
                numFrames = pixelDist * framesPerPixel;
                xStep = xDist / numFrames;
                yStep = yDist / numFrames;
                for (let j = 0; j < numFrames; j++) {
                    strokeCoords.push([point[0] + xStep * (j + 1), point[1] + yStep * (j + 1)]);
                }
            }
        }
        animationCoords.push(strokeCoords);
    }

    requestAnimationFrame(() => animationFunc(transformedStrokes, animationCoords, 0, 0, transformedStrokes[0][0]));
};

const isVowel = (letter) => {
    const vowels = ['A', 'E', 'I', 'O', 'U'];
    return vowels.includes(letter.toUpperCase());
};

const Level = ({ levelNum, completeLevel, fps }) => {
    const [letterInd, setLetterInd] = useState(0);
    const [isDrawing, setIsDrawing] = useState(false);
    const [reloadKey, setReloadKey] = useState(-1);
    const [transformedStrokes, setTransformedStrokes] = useState(null);
    const [isButtonDisabled, setIsButtonDisabled] = useState(false);
    const [replayImageSrc, setReplayImageSrc] = useState('');


    isAnimating = useRef(true);
    const tracePointInd = useRef(null);
    const traceStrokeInd = useRef(null);
    context = useRef(null);
    imgScale = useRef(null);
    const pointsScale = useRef(null);
    const letterHeight = useRef(null);
    const lastDrawnPoint = useRef({ x: 0, y: 0 });
    const prevD2ToPoint = useRef(null);
    const betweenStrokes = useRef(null);
    const performanceData = useRef({
        time: new Date().getTime(),
        letters: {}
    });

    const hasTracked = useRef(false);

    const resetPrevDist = () => {
        prevD2ToPoint.current = (letterHeight.current * 2) ** 2;
    };

    const handleReplay = () => {
        if (isAnimating.current) return;
    
        resetPrevDist();
        setIsButtonDisabled(true);
        setTimeout(() => setIsButtonDisabled(false), 5000); // 5 seconds cooldown

        // Replay the animation and sound
        audioPlayer.src = letterSounds[levels[levelNum].letters[letterInd]];
        audioPlayer.play();
        animateStrokes(transformedStrokes, pointsScale.current[0], fps);
    };

    const playLetterSoundInSequenceAndAdvanceLetterWithDelay = () => {
        try {
            if (audioPlayer.paused) {
                audioPlayer.src = letterSounds[levels[levelNum].letters[letterInd]];
                var playPromise = audioPlayer.play();
                if (playPromise !== undefined) {
                    playPromise.then(_ => {

                        audioPlayer.onended = () => {
                            PlayGreatJobAndLoadNextLetter(1.5);
                        };
                    })
                        .catch(error => {
                            console.log(error);
                        });
                }
            }


        } catch (error) {
            console.error('Error in playSoundsInSequenceAndAdvanceLetterWithDelay:', error);
        }
    };

    const PlayGreatJobAndLoadNextLetter = (delay) => {
        setTimeout(() => {
            if (audioPlayer.paused) {
                audioPlayer.src = levelSounds.greatJob;
                var playPromise = audioPlayer.play();
                if (playPromise !== undefined) {
                    playPromise.then(_ => {
                        audioPlayer.onended = () => {
                            advanceLetterWithDelay(letterInd, setLetterInd, traceStrokeInd, tracePointInd, resetPrevDist, 2); // 2 seconds delay
                        };
                    })
                        .catch(error => {
                            console.log(error);
                        });
                }
            }
        }, delay * 1000);
    }

    const loadImg = async (event) => {
        const letter = levels[levelNum].letters[letterInd];
        const imgSrc = isVowel(letter) ? '/pngs/replay-vowel.png' : '/pngs/replay-consonant.png';
        setReplayImageSrc(imgSrc);

        audioPlayer.src = letterSounds[letter];
        audioPlayer.play();
        isAnimating.current = true;

        if (!hasTracked.current) {
            let curNumMistakes = getLetterAttrib(performanceData.current, letter, 'numTries') || 0;
            saveLetterAttrib(performanceData.current, letter, 'numTries', curNumMistakes + 1);
        }

        hasTracked.current = true;

        document.getElementById('level').style.backgroundColor = colorByLetter[letter] || colorByLetter['default'];
        betweenStrokes.current = false;

        const letterImg = event.target;
        const scaleFactor = window.innerHeight * 0.0009;
        imgScale.current = scaleFactor;
        letterImg.height = scaleFactor * letterImg.naturalHeight;
        letterHeight.current = letterImg.height;

        context.current.clearRect(0, 0, canvas.width, canvas.height);

        let points = levels[levelNum].tracePoints[letter];

        pointsScale.current = [letterImg.width / points.dims[0], letterImg.height / points.dims[1]];

        const newTransformedStrokes = [];
        for (let inStroke of points.points) {
            newTransformedStrokes.push(transformStroke(inStroke, [letterImg.width, letterImg.height], pointsScale.current));
        }

        setTransformedStrokes(newTransformedStrokes);

        animateStrokes(newTransformedStrokes, pointsScale.current[0], fps);
        await new Promise((resolve) => setTimeout(resolve, 0.5 * 1000));

        tracePointInd.current = 0;
        traceStrokeInd.current = 0;
        resetPrevDist();
    };

    useEffect(() => {
        if (letterInd >= levels[levelNum].letters.length) {
            performanceData.current.time = new Date().getTime() - performanceData.current.time;
            completeLevel(performanceData.current);
        }
    }, [letterInd]);

    useEffect(() => {
        const canvas = document.getElementById('canvas');
        const curContext = canvas.getContext('2d');
        context.current = curContext;

        const rect = canvas.getBoundingClientRect();

        const handleTouchStart = (event) => {
            if (isAnimating.current) {
                return;
            }

            hasTracked.current = false;

            lastDrawnPoint.current = {
                x: event.touches[0].clientX - rect.left,
                y: event.touches[0].clientY - rect.top
            };

            context.current.fillStyle = 'black';
            context.current.beginPath();
            context.current.ellipse(lastDrawnPoint.current.x, lastDrawnPoint.current.y, DRAW_LINE_WIDTH / 2, DRAW_LINE_WIDTH / 2, 0, 0, Math.PI * 2);
            context.current.fill();
            let checkResult = checkDrawnPoint(prevD2ToPoint.current, transformedStrokes[traceStrokeInd.current][tracePointInd.current], lastDrawnPoint.current, pointsScale.current[0]);

            if (checkResult.hit) {
                advancePoint(tracePointInd, traceStrokeInd, transformedStrokes, resetPrevDist, betweenStrokes);
                if (traceStrokeInd.current >= transformedStrokes.length) {
                    playLetterSoundInSequenceAndAdvanceLetterWithDelay();
                } else if (DRAW_ALL_DOTS) {
                    drawTarget(tracePointInd.current, traceStrokeInd.current, transformedStrokes, imgScale.current);
                }
            }

            setIsDrawing(true);
        };

        const handleTouchMove = async (event) => {
            if (!isDrawing) return;
            if (isAnimating.current) return;

            const x = event.touches[0].clientX - rect.left;
            const y = event.touches[0].clientY - rect.top;

            context.current.beginPath();
            context.current.moveTo(lastDrawnPoint.current.x, lastDrawnPoint.current.y);
            context.current.lineTo(x, y);
            context.current.strokeStyle = 'black';
            context.current.lineWidth = DRAW_LINE_WIDTH;
            context.current.lineCap = 'round';
            context.current.stroke();

            lastDrawnPoint.current = { x, y };

            if (transformedStrokes[traceStrokeInd.current] && tracePointInd.current < transformedStrokes[traceStrokeInd.current].length) {
                let checkResult = checkDrawnPoint(prevD2ToPoint.current, transformedStrokes[traceStrokeInd.current][tracePointInd.current], lastDrawnPoint.current, pointsScale.current[0]);
                prevD2ToPoint.current = checkResult.dist;

                if (isDrawing && !checkResult.good && !betweenStrokes.current) {
                    setIsDrawing(false);
                    document.getElementById('level').style.backgroundColor = '#E84855';
                    if (audioPlayer.paused) {
                        audioPlayer.src = levelSounds.tryAgain;
                        var playPromise = audioPlayer.play();
                        if (playPromise !== undefined) {
                            playPromise.then(_ => {
                            })
                                .catch(error => {
                                    console.log(error);
                                });
                        }
                    }
                    // if (levelAudio.current) {
                    //     levelAudio.current.pause();
                    //     levelAudio.current.currentTime = 0;
                    // }
                    // levelAudio.current = new Audio(levelSounds.tryAgain)
                    // levelAudio.current.play();
                    await new Promise((resolve) => setTimeout(resolve, 2.5 * 1000));

                    setReloadKey(Math.random());
                } else if (checkResult.hit) {
                    advancePoint(tracePointInd, traceStrokeInd, transformedStrokes, resetPrevDist, betweenStrokes);
                    if (traceStrokeInd.current >= transformedStrokes.length) {
                        playLetterSoundInSequenceAndAdvanceLetterWithDelay();
                    } else if (DRAW_ALL_DOTS) {
                        drawTarget(tracePointInd.current, traceStrokeInd.current, transformedStrokes, imgScale.current);
                    }
                }
            } else {
                if (traceStrokeInd.current >= transformedStrokes.length) {
                    playLetterSoundInSequenceAndAdvanceLetterWithDelay();
                } else {
                    advancePoint(tracePointInd, traceStrokeInd, transformedStrokes, resetPrevDist, betweenStrokes);
                }
            }
        };


        const handleTouchEnd = () => {
            betweenStrokes.current = false;
            setIsDrawing(false);
        };

        canvas.addEventListener('touchstart', handleTouchStart, { passive: true });
        canvas.addEventListener('touchmove', handleTouchMove, { passive: true });
        canvas.addEventListener('touchend', handleTouchEnd, { passive: true });
        canvas.addEventListener('touchcancel', handleTouchEnd, { passive: true });

        return () => {
            canvas.removeEventListener('touchstart', handleTouchStart);
            canvas.removeEventListener('touchmove', handleTouchMove);
            canvas.removeEventListener('touchend', handleTouchEnd);
            canvas.removeEventListener('touchcancel', handleTouchEnd);
        };
    }, [isDrawing, transformedStrokes]);

    return (
        <div id='level' className="level">
            <button 
                id="replayButton" 
                onClick={handleReplay} 
                disabled={isButtonDisabled}
                style={{ 
                    position: 'absolute',
                    top: '10px', 
                    left: '10px', 
                    width: '200px', 
                    height: '200px', 
                    zIndex: 10,
                    backgroundColor: 'transparent',
                    border: 'none',
                    cursor: 'pointer'
                }}
            >
                <img src={replayImageSrc} alt="Replay" style={{ width: '100%', height: '100%' }} />
            </button>
            <motion.div
                key={levels[levelNum].letters[letterInd] + '-level-' + reloadKey}
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
            >
                <img className='centered-and-cropped' id='letterImage' src={`letters/${levels[levelNum].letters[letterInd]}.png`} onLoad={loadImg} />
                <canvas
                    id="canvas"
                    width={canvasWidth}
                    height={canvasHeight}
                    style={{ border: 'none' }}
                />
            </motion.div>
        </div>
    );
};

export default Level;
