import { React, useState } from 'react'
import { motion } from 'framer-motion'

import { clearUserData } from './utils/user'
import './Settings.css'

const Settings = ({ back }) => {
    const [clearAskSure, setClearAskSure] = useState(false)

    return <div style={{ fontSize: 100, textAlign: 'center', margin: 'auto', marginTop: '10rem' }}>

        <motion.button
            whileTap={{ scale: 0.9, filter: 'brightness(50%)' }}
            transition={{ duration: .1 }}
            className="backButton"
            onClick={() => {
                back()
            }}
        >
            <img src='./icons/back.svg' height='50rem' alt='back' />
        </motion.button>

        <div style={{ fontSize: 60 }}>
            <div style={{ display: 'flex', alignItems: 'center', width: '70vw', justifyContent: 'space-between', maxWidth: '50rem' }}>
                <div>Borrar datos de usuario</div>
                {!clearAskSure && (
                    <motion.button
                        whileTap={{ scale: 0.9, filter: 'brightness(50%)' }}
                        transition={{ duration: .1 }}
                        style={{
                            backgroundColor: '#84428a',
                            border: 'none',
                            borderRadius: '7rem',
                            width: '6rem',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            height: '3rem'
                        }}
                        onClick={() => {
                            setClearAskSure(true)
                        }}
                    >
                        <img src='./icons/play.svg' height='40rem' />
                    </motion.button>
                )}
                {clearAskSure && (
                    <div style={{ display: 'flex', height: '3rem' }}>
                        <motion.button
                            whileTap={{ scale: 0.9, filter: 'brightness(50%)' }}
                            transition={{ duration: .1 }}
                            style={{
                                background: 'none',
                                border: 'none',
                                paddingTop: '0.3rem',
                                borderRadius: '7rem',
                                width: '4rem',
                                marginRight: '0.5rem',
                                height: '3rem'
                            }}
                            onClick={() => {
                                setClearAskSure(false)
                            }}
                        >
                            <img src='./icons/ex.svg' height='40rem' />
                        </motion.button>
                        <motion.div
                            whileTap={{ scale: 0.9, filter: 'brightness(50%)' }}
                            transition={{ duration: .1 }}
                            style={{
                                backgroundColor: '#84428a',
                                border: 'none',
                                borderRadius: '7rem',
                                color: 'white',
                                height: '3rem',
                                fontSize: 40,
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                padding: '.2rem 2rem 0'
                            }}
                            onClick={() => {
                                clearUserData()
                                window.location.reload()
                            }}
                        >
                            Estoy seguro
                        </motion.div>
                    </div>
                )}
            </div>
        </div>
    </div>
}

export default Settings