import { React, useEffect, useState } from "react"
import { motion, AnimatePresence, MotionConfig } from "framer-motion"
import "./App.css"

import Home from "./Home"
import Level from "./Level"
import AskStart from "./AskStart"
import LevelComplete from "./LevelComplete"
import Settings from "./Settings"
import RoundComplete from "./RoundComplete"


import { completeRound, getUserLevel, saveLevel, setUserLevel, userIsInProgress } from "./utils/user"
import { levels } from "./utils/levelData"

var FPS
if (!window.requestAnimationFrame) {
  window.requestAnimationFrame =
    window.mozRequestAnimationFrame ||
    window.webkitRequestAnimationFrame;
}

// Function that returns a Promise for the FPS
const getFPS = async () => {
  return new Promise(resolve =>
    requestAnimationFrame(t1 =>
      requestAnimationFrame(t2 => {
        let curFPS = 1000 / (t2 - t1)
        if (curFPS > 300 || curFPS < 5) curFPS = 60
        resolve(curFPS)
      })
    )
  )
}



const App = () => {
  const [pageSet, setPageSet] = useState({ 'home': true })

  const easySetPage = (page) => {
    for (let key of Object.keys(pageSet)) {
      pageSet[key] = false
    }
    pageSet[page] = true
    setPageSet({ ...pageSet })
  }

  useEffect(() => {
    const run = async () => {
      let sum = 0
      for (let i = 0; i < 10; i++) {
        sum += await getFPS()
      }
      FPS = sum / 10
      console.log(FPS);
    }
    run()
  }, [])

  let startToPage = userIsInProgress() ? 'askStart' : 'level'

  return <MotionConfig transition={{ duration: 0.5 }}>
    <AnimatePresence mode="wait">
      {pageSet['home'] && (
        <motion.div
          key="home"
          className="App"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <Home start={() => easySetPage(startToPage)} settings={() => easySetPage('settings')} />
        </motion.div>
      )}
      {pageSet['askStart'] && (
        <motion.div
          key="askStart"
          className="App"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <AskStart toLevelNum={getUserLevel()} goToLevel={() => easySetPage('level')} />
        </motion.div>
      )}
      {pageSet['settings'] && (
        <motion.div
          key="settings"
          className="App"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <Settings back={() => easySetPage('home')} />
        </motion.div>
      )}
      {pageSet['level'] && (
        <motion.div
          key="level"
          className="App"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <Level levelNum={getUserLevel()} fps={FPS} completeLevel={(performanceData) => {
            saveLevel(performanceData)
            setUserLevel(getUserLevel() + 1)

            if (getUserLevel() >= levels.length) {
              easySetPage('roundComplete')
              completeRound().catch(e => console.error('error completing round: ', e))
            } else {
              easySetPage('levelComplete')
            }
          }
          } />
        </motion.div>
      )}
      {pageSet['levelComplete'] && (
        <motion.div
          key="levelComplete"
          className="App"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <LevelComplete goToNext={() => easySetPage('level')} />
        </motion.div>
      )}
      {pageSet['roundComplete'] && (
        <motion.div
          key="roundComplete"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <RoundComplete toHome={() => easySetPage('home')} goAgain={() => easySetPage('level')} />
        </motion.div>
      )}
    </AnimatePresence>
  </MotionConfig>
}

export default App
